    font-weight: 700;
} */

.buy-sell-toggle.left {
      left: -1px;
}

.buy-sell-toggle.right {
      right: -1px;
}
.buy-sell-toggle-block {
    display: flex;
    flex-direction: row;
    align-items: center;
