<template>
    <div class="exchange">
       <!-- <div class="header-block">
            <p class="exchange-header">{{isDemo ? $localizeFilter('exchange_demo') : $localizeFilter('exchange')}}</p>
            <p class="exchange-text">{{$localizeFilter('exchange_text')}}</p>
        </div> -->
      <!-- <img class="statistics-small" src="@/assets/newImages/statistics_small.png" @click="showStatisticsModal=true"> -->
      <div class="content-block" v-if="selectedCoinsPair">
      <div class="main-block" id="exchangeMainBlock">
        <div class="coins-pairs-block desktop">
            <CryptoRate class="crypto-rate" :coinsPair="selectedCoinsPair" @changeCoinsPair="changeCoinsPair"/>
            <CoinsPairsSearch class="coins-pairs-search" @loadData="loadData"/>
        </div>

        <div class="coins-pairs-block mobile" @click="showChangeCoinsPairModal=true">
            <CryptoRate class="crypto-rate" :coinsPair="selectedCoinsPair" @changeCoinsPair="changeCoinsPair"/>
            <button class="arrow-button">
                <i class="material-icons white-text">keyboard_arrow_right</i>
            </button>
        </div>

        <BuySellToggle :title="null" :variants="actions" @toggle="toggle" />
        <div class="options-block">
        <ExchangeOptions class="exchange-options" :options="orderTypes" @selectOption='selectOption'/>
        <button class="info-button" @click="showOrderTypeInfoModal=true"></button>
        </div>
        <ExchangeFields :coinsPair="selectedCoinsPair" :type="selectedOrderType" :actionType="selectedAction"/>
        <OrdersList class="orders-list" />
      </div>

      <div class="statistics-block">
      <!-- <div class="statistics-block" :style="`height: ${statisticsHeight}px;`"> -->
        <!-- <div id="widthAnchor"></div> -->
        <ExchangeStatistics :width="staticticsWidth" v-if="showStatistics"/>
      </div>
      </div>
     <!-- <transition name="move-right"> -->
     <ChangeCoinsPairModal v-if="showChangeCoinsPairModal" @loadData="loadData" @closeModal="closeModals"/>
     <!-- </transition> -->
     <!-- <StatisticsModal v-if="showStatisticsModal" @closeModal="closeModals"/> -->
     <OrderTypeInfoModal v-if="showOrderTypeInfoModal" :orderTypes="orderTypes" @closeModal="closeModals"/>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import CryptoRate from '@/components/exchange/CryptoRate.vue'
import ExchangeStatistics from '@/components/exchange/ExchangeStatistics.vue'
import BuySellToggle from "@/components/BuySellToggle.vue"
import ExchangeOptions from '@/components/exchange/ExchangeOptions.vue'
import OrdersList from '@/components/exchange/OrdersList.vue'
import ChangeCoinsPairModal from '@/components/newModals/ChangeCoinsPairModal.vue'
import StatisticsModal from '@/components/newModals/StatisticsModal.vue'
import OrderTypeInfoModal from '@/components/newModals/OrderTypeInfoModal.vue'
import localizeFilter from '@/filters/localize.filter'
import ExchangeFields from '../components/exchange/ExchangeFields.vue'
import CoinsPairsSearch from '../components/exchange/CoinsPairsSearch.vue'

export default defineComponent({
    props: {
        firstCoinOverride: {
            type: String,
            default: null
        },
        lastCoinOverride: {
            type: String,
            default: null
        },
        ordersListPageOverride: {
            type: Number,
            default: 0
        },
        isDemoOverride: {
            type: Boolean,
            default: false
        }
    },
    components: {
        CryptoRate,
        ExchangeStatistics,
        BuySellToggle,
        ExchangeOptions,
        OrdersList,
        ChangeCoinsPairModal,
        StatisticsModal,
        OrderTypeInfoModal,
        ExchangeFields,
        CoinsPairsSearch
    },
    setup() {
    },
    data: () => ({
      showChangeCoinsPairModal: false,
    //   showStatisticsModal: false,
      showOrderTypeInfoModal: false,
      staticticsWidth: 0,
      showStatistics: false,
    }),
    computed: {
        defaultCoinsPair() {
            return this.$store.getters.exchangeDefaultCoinsPair
        },
        selectedCoinsPair() {
            return this.$store.getters.exchangeSelectedCoinsPair
        },
        actions() {
            return this.$store.getters.exchangeActions
        },
        selectedAction() {
            return this.$store.getters.exchangeSelectedAction
        },
        orderTypes() {
            return this.$store.getters.exchangeOrderTypes
        },
        selectedOrderType() {
            return this.$store.getters.exchangeSelectedOrderType
        },
        isDemo() {
            return this.$store.getters.isDemo
        },
    },
    async mounted() {
            this.staticticsWidth = window.innerWidth - 82 - 450
            // console.log('CALCULATE WIDTH = ', this.staticticsWidth)
            this.showStatistics = true //window.innerWidth > 1100
            this.$store.dispatch('showLoader')
            try {
                await this.$store.dispatch('getExchangeCoins')
                await this.$store.dispatch('getExchangeCoinsPairs')
                await this.$store.dispatch('setDemo', {isDemo: this.isDemoOverride})
                if (this.firstCoinOverride && this.lastCoinOverride) {
                    await this.$store.dispatch('setSelectedCoinsPair', {firstCoin: this.firstCoinOverride, lastCoin: this.lastCoinOverride})
                    await this.loadData()
                    this.$store.dispatch('hideLoader')
                } else {
                    if (!this.selectedCoinsPair) {
                        const parsed = JSON.parse(JSON.stringify(this.defaultCoinsPair))
                        await this.$store.dispatch('setSelectedCoinsPair', parsed)
                        // // console.log(this.defaultCoinsPair)
                        // // console.log(this.selectedCoinsPair)
                        await this.loadData()
                        this.$store.dispatch('hideLoader')
                    }
                }
            } catch(e) {
                this.$error(e)
                this.$store.dispatch('hideLoader')
            }

            // this.$nextTick(()=>{
            //     // console.log(document.getElementById('exchangeMainBlock').offsetHeight)
            //     this.statisticsHeight = document.getElementById('exchangeMainBlock').offsetHeight;
            // })
    },
    methods: {
        async loadData(){
            await this.$store.dispatch('subscribeExchangeNotifications')
            await this.$store.dispatch('getExchangeOrders', {pair: `${this.selectedCoinsPair.firstCoin}/${this.selectedCoinsPair.lastCoin}`})
            await this.$store.dispatch('getOrderBook')
        },
        toggle(index) {
            this.$store.commit('SET_SELECTED_EXCHANGE_ACTION', this.actions[index])
        },
        async selectOption(idx) {
            await this.$store.dispatch('setSelectedExchangeOrderType', {type: this.orderTypes[idx]})
            // this.$nextTick(()=>{
            //     // // console.log(document.getElementById('exchangeMainBlock').offsetHeight)
            //     this.statisticsHeight = document.getElementById('exchangeMainBlock').offsetHeight;
            //     // // console.log(this.statisticsHeight)
            //     this.$forceUpdate()
            // })
        },
        changeCoinsPair() {
            this.showChangeCoinsPairModal = true
        },
        closeModals() {
            this.showChangeCoinsPairModal = false
            // this.showStatisticsModal = false
            this.showOrderTypeInfoModal = false
        },
    }
})
</script>

