<template>
    <div class="exchange-options">
        <div class="window light trigger-block" @click="triggerDropdown">
            <span class="placeholder">{{$localizeFilter('type')}}</span>
            <div class="wrapper">
            <label style="color: #EAEAEA;">{{$localizeFilter(prefix+options[selectedIndex])}}</label>
            <button class="trigger-button">
                <img style="width:10px; height: 6px;" :src="require('@/assets/newImages/white_arrow.png')">
            </button>
            </div>
        </div>
        <div class="window light dropdown-block" v-if="isOpen">
        <ul>
            <li v-for="(option,idx) in options" :key="idx" :class="selectedIndex==idx ? 'selected' : ''" @click="selectOption(idx)">
                {{$localizeFilter(prefix+option)}}
            </li>
        </ul>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: [
        'options', 'prefix'
    ],
    data: ()=>({
        isOpen: false ,
        selectedIndex: 0
    }),
    setup() {
        
    },
    methods: {
        triggerDropdown() {
            this.isOpen = !this.isOpen
        },
        selectOption(idx) {
            this.selectedIndex = idx
            this.$emit('selectOption', idx)
            this.isOpen = false
        }
    }
})
</script>

