</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    props: ['orderTypes'],
    setup() {
    },
    data: () => ({
    tab: 0,
    showWindow: false

    }),
    mounted() {
        if (window.innerWidth < 768) {
            setTimeout(()=>{
                this.showWindow = true
            },500)
        } else {
            this.showWindow = true
        }
    },
    methods: {
        closeModal() {
            this.$emit('closeModal')
        }
    }
})
</script>

